








import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';

@Component({})
export default class App extends Vue {

  private fullScreen: boolean = false;

  private mounted() {
    window.addEventListener('keyup', (e) => {
       switch (e.key) {
         case 'ArrowLeft': {
           this.prevSlide();
           break;
         }
         case 'ArrowRight': {
           this.nextSlide();
           break;
         }
         case 'f': {
           this.toggleFullScreen();
           break;
         }
         case 'Escape': {
           if (this.fullScreen) {
             this.toggleFullScreen();
             break;
           }
           break;
         }
       }
    });
  }

  private toggleFullScreen() {
    this.fullScreen = !this.fullScreen;
    const el = document.documentElement;
    const rfs = el.requestFullscreen;
    rfs.call(el);
  }

  private nextSlide() {
    if (this.$route.name) {
      const nextRoute = parseInt(this.$route.name, 0) + 1;
      this.$router.push({name: nextRoute.toString()});
    }
  }

  private prevSlide() {
    if (this.$route.name && parseInt(this.$route.name, 0) > 1) {
      const nextRoute = parseInt(this.$route.name, 0) - 1;
      this.$router.push({name: nextRoute.toString()});
    }
  }
}
